import React, { Suspense } from 'react';
import { Redirect, Route, Switch } from 'react-router-dom';

const About = React.lazy(() =>
  import(/* webpackChunkName: "profile-about" */ './about')
);
const Education = React.lazy(() =>
  import(/* webpackChunkName: "profile-education" */ './education')
);
const Experience = React.lazy(() =>
  import(/* webpackChunkName: "profile-experience" */ './experience')
);
const Links = React.lazy(() =>
  import(/* webpackChunkName: "profile-links" */ './links')
);
const Contact = React.lazy(() =>
  import(/* webpackChunkName: "profile-contact" */ './contact')
);
const ProfileView = React.lazy(() =>
  import(/* webpackChunkName: "profile-default" */ './default')
);

const Profile = ({ match }) => (
  <Suspense fallback={<div className="loading" />}>
    <Switch>
      <Redirect exact from={`${match.url}/`} to={`${match.url}/view`} />
      <Route
        path={`${match.url}/view`}
        render={(props) => <ProfileView {...props} />}
      />
      <Route
        path={`${match.url}/about`}
        render={(props) => <About {...props} />}
      />
      <Route
        path={`${match.url}/education`}
        render={(props) => <Education {...props} />}
      />
      <Route
        path={`${match.url}/experience`}
        render={(props) => <Experience {...props} />}
      />
      <Route
        path={`${match.url}/links`}
        render={(props) => <Links {...props} />}
      />
      <Route
        path={`${match.url}/contact`}
        render={(props) => <Contact {...props} />}
      />
      <Redirect to="/error" />
    </Switch>
  </Suspense>
);

export default Profile;
